dark-mode {
  --main-accent-color: #4f2ee8;
  --secondary-accent-color: #eac645;
  --main-color: #b4afaf;
  --secondary-color: #fff;
  --main-bg-color: #202024;
}

.slider {
  transform: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, fill .3s ease-in-out, transform .3s ease-in-out;
}

.circle:before {
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, fill .3s ease-in-out, transform .3s ease-in-out;
}

